import { useCallback, useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { Fn } from "../../../helper/function";
import { PaginationRequest, TableColumn } from "../../../models/baseModel";
import { MailTemplate } from "../../../services/adminService";
import { MyBox } from "../../core/MyBox";
import { MyButton } from "../../core/MyButton";
import { OrderByModel, PageInfoModel } from "../../core/TableModel";
import TableComponent from "../../core/TableNewComponent";
import { UIFormatDateTimeVNComponent } from "../../core/UI_FormatComponent";

export default function ManageTemplateMailIndexComponent() {
    const column: any = [
        new TableColumn({
            title: "STT", key: "stt", disabled: true, render: (data: any, row: any, index: number) =>
                Fn.RenderSTT(data, row, index, pageInfo)
        }),
        new TableColumn({
            title: "Mã", dataIndex: "code", key: "code", render: (data: string, row: any) =>
                <Link to={`/admin/template/edit/${row.key}`}>{data} </Link>
        }),
        new TableColumn({ title: "Tiêu đề", key: "title" }),
        new TableColumn({
            title: "App name", key: "apps", render: (value: any) =>
                <span>{value.map((data: any) => data.name).join(", ")}</span>
        }),
        new TableColumn({
            title: "Ngày tạo", key: "createdAt", render: (data: string) =>
                <UIFormatDateTimeVNComponent value={data} />
        }),
        new TableColumn({
            title: "Ngày cập nhật", key: "updatedAt", render: (data: string) =>
                <UIFormatDateTimeVNComponent value={data} />
        })
    ];

    const [pageInfo, setPageInfo] = useState<PageInfoModel>(new PageInfoModel());
    const [orderBy, setOrderBy] = useState<OrderByModel>(new OrderByModel());
    const [data, setData] = useState([]);
    const [isLoading, setIsLoading] = useState(false);

    const loadData = useCallback(() => {
        setIsLoading(true);
        let pagination = new PaginationRequest(pageInfo.pageNumber, pageInfo.pageSize, orderBy);
        MailTemplate.GetList(pagination)
            .then((res: any) => {
                if (res.isSuccess) {
                    setPageInfo(new PageInfoModel(res));
                    setData(res.data.pagedData);
                }
            })
            .finally(() => setIsLoading(false));
    }, [pageInfo.pageSize, pageInfo.pageNumber, orderBy]);

    useEffect(() => {
        loadData();
    }, [loadData]);

    return (
        <MyBox
            title="Quản lý template mail"
            isLoading={isLoading}>
            <Link to={"/admin/template/add"}> <MyButton value="Thêm mới" /></Link>
            <TableComponent
                pageInfo={pageInfo}
                setPageInfo={setPageInfo}
                orderBy={orderBy}
                setOrderBy={setOrderBy}
                column={column}
                dataSource={data}
                loading={isLoading}
                loadData={loadData} />
        </MyBox>
    );
}